body {
  background-blend-mode: normal, normal, screen, overlay, normal;
  background: #bceaf7 no-repeat;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h2 {
  color: #3d2b74;
  font-size: 32px;
}

p {
  color: #3d2b74;
  text-align: justify;
  font-size: 20px;
}

.cat-info {
  flex-direction: column;
  justify-content: left;
  gap: 20px;
  padding-left: 20px;
  display: flex;
}

.cat-info p {
  width: 600px;
  font-size: 20px;
}

.loader {
  box-sizing: content-box;
  background: #fff;
  border: 2px solid #b1d0ee;
  border-bottom-width: 50px;
  border-radius: 100%;
  width: 96px;
  height: 48px;
  animation: 3s linear infinite yinYang;
  position: relative;
}

.loader:before {
  content: "";
  box-sizing: content-box;
  background: #fff;
  border: 18px solid #b1d0ee;
  border-radius: 100%;
  width: 12px;
  height: 12px;
  position: absolute;
  top: 50%;
  left: 0;
}

.loader:after {
  content: "";
  box-sizing: content-box;
  background: #b1d0ee;
  border: 18px solid #fff;
  border-radius: 100%;
  width: 12px;
  height: 12px;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes yinYang {
  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  -webkit-appearance: none;
  -moz-appearance: none;
  cursor: pointer;
  color: #000;
  background: #b1d0ee;
  border: none;
  border-radius: 6px;
  outline: none;
  width: 140px;
  height: 35px;
  margin: 20px;
  padding: 8px;
  display: inline-block;
  position: relative;
  box-shadow: 2px 2px 8px #d0dad4;
}

